.name{
    padding-left: 10px;
    padding-top: 10px;
    color: brown;
    display: flex;
    right: 3px;
    
}
.profile{
    width: 30px;
    height: 30px;
    border: 1px solid ;
    border-radius: 30px;
}
body{
    flex-wrap: wrap;
    width: 100vw;
    height: 100vh;
    background: #f09433; 
background: -moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%); 
background: -webkit-linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
background: linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f09433', endColorstr='#bc1888',GradientType=1 );
}
header{
    width: 100%;
    height: 100px;
}
h1{ 
    position: absolute;
    left: 10px;
    font-size: 40px;
    color: white;
    font-style:oblique
}
#link{
    position: absolute;
    top: 60px;
    color: aqua;
}
.main{
    border: 1px solid black;
    box-shadow: slategrey;
    width: 230px;
    height: 300px; 
    background-color: white; 
    margin-bottom: 30px;   
}

.postImage{
    width: 200px;
    height: 150px;
}

.location{
    font-size: 8px;
    display: flex;
    position: relative;
    bottom: 34px;
    left: 45px;
}
.likes{
    padding-left: 10px;
    float: left;
}
.date{
    float: right;
    font-size: 10px;
}

